import { Table } from '@ali/4ever-mo';
import { createTableInstance, createControllerInstance, getTableFromController } from '../helpers';

describe('bi/table/commands/resizeTableWidth', () => {
  it('autofit table', () => {
    const originColsWidth = [40, 60];
    const table = createTableInstance({
      table: [['A', 'B']],
      colsWidth: originColsWidth,
      isAutofit: true,
    });
    const controller = createControllerInstance([table]);
    controller.command('resizeTableWidth', table, 20, 200);
    const newTable = getTableFromController(controller);
    expect(Table.isAutofitWidth(newTable)).toBe(false);
  });
});
